/**
 *  Define scss variables here.
 *
 *  Available options for Bootstrap:
 *  http://getbootstrap.com/customize/
 *
 */
.root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

:global .navbar-inverse {
  border: 0;
}

:global .navbar-inverse .navbar-nav > .active > a,
:global .navbar-inverse .navbar-nav > .active > a:hover,
:global .navbar-inverse .navbar-nav > .active > a:focus,
:global .navbar-inverse .navbar-nav > .open > a,
:global .navbar-inverse .navbar-nav > .open > a:hover,
:global .navbar-inverse .navbar-nav > .open > a:focus {
  background-color: transparent;
}

:global .navbar-inverse .navbar-brand {
  padding-top: 20px;
  padding-bottom: 20px;
  height: 60px;
}

:global .navbar-inverse .navbar-toggle {
  border-color: transparent;
  background-color: transparent !important;
  margin-top: 13px;
  margin-bottom: 13px;
}

:global .navbar-inverse .navbar-toggle .icon-bar {
  background-color: #9d9d9d;
  transition: backgroundColor 0.7s ease 0s;
}

:global .navbar-inverse .navbar-toggle:hover .icon-bar {
  background-color: white;
}

@media (min-width: 768px) {
  :global .navbar-inverse .navbar-nav > li > a {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

:global footer {
  padding-bottom: 20px;
  color: #9d9d9d;
  background-color: #222;
  font-size: 0.8em;
}

:global footer img, :global footer h4 {
  margin-top: 30px;
  margin-bottom: 20px;
}

:global footer a {
  color: #9d9d9d;
  text-decoration: none !important;
  cursor: pointer;
}

:global footer a:hover {
  color: white;
}

:global footer .col-sm-3 > div {
  margin: 8px 0;
}

:global footer .fa {
  margin-right: 8px;
}

.content {
  flex: 1;
  position: relative;
  margin-top: 50px;
  min-height: 500px;
}



