@import '../../theme/variables.scss';

.root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

:global {
  .navbar-inverse {
    border: 0;

    .navbar-nav > .active > a,
    .navbar-nav > .active > a:hover,
    .navbar-nav > .active > a:focus,
    .navbar-nav > .open > a,
    .navbar-nav > .open > a:hover,
    .navbar-nav > .open > a:focus {
      background-color: transparent;
    }

    .navbar-brand {
      padding-top: 20px;
      padding-bottom: 20px;
      height: 60px;
    }

    .navbar-toggle {
      border-color: transparent;
      background-color: transparent !important;
      margin-top: 13px;
      margin-bottom: 13px;
      .icon-bar {
        background-color: #9d9d9d;
        transition: backgroundColor 0.7s ease 0s;
      }
    }

    .navbar-toggle:hover .icon-bar {
      background-color: white;
    }

    @media (min-width: 768px) {
      .navbar-nav > li > a {
        padding-top: 20px;
        padding-bottom: 20px;
      }
    }
  }

  footer {
    padding-bottom: 20px;
    color: #9d9d9d;
    background-color: #222;
    font-size: 0.8em;
    img, h4 {
      margin-top: 30px;
      margin-bottom: 20px;
    }

    a {
      color: #9d9d9d;
      text-decoration: none !important;
      cursor: pointer;
    }
    a:hover {
      color: white;
    }

    .col-sm-3 > div {
      margin: 8px 0;
    }

    .fa {
      margin-right: 8px;
    }
  }
}

.content {
  flex: 1;
  position: relative;
  margin-top: 50px;
  min-height: 500px;
}




